import Vue from "vue";
import VueRouter from "vue-router";
import Main from "../views/Main.vue";
import Privacy from "@/views/Privacy";
import Test from "@/views/Test";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Main",
    component: Main
  },
  {
    path: "/privacy_policy",
    name: "PrivacyPolicy",
    component: Privacy
  },
  {
    path: "/test",
    name: "Test",
    component: Test
  }
];

const router = new VueRouter({
  routes
});

export default router;
